import React from 'react'

const useList = (List, selectNo, writeMode) => {
 return (
	 List.map((item, index) => (
		 <tr key={item.no}>
			 <td>
				 <input type="checkbox" name="inpChk" className="ch_check hide" id="inpChk11" />
				 <label htmlFor="inpChk11" className="label single" />
			 </td>
			 <td>{item.no}</td>
			 <td>{item.createDate}</td>
			 <td className="ellipsis"><a onClick={()=>{selectNo(item.no)
				 writeMode(2)
			 }}>{item.title}</a></td>
			 <td>{item.createAdmin}</td>
		 </tr>
		 )
	 )

 )
}

export default useList
