import CommonAxios from '../Axios/CommonAxios'

export default {
	postNotice (data) {
		return CommonAxios(({
			url: '/api/notice/write',
			method: 'POST',
			data
		}))
	},

	getNotice (params) {
		return CommonAxios(({
			url: 'api/notice/list',
			method: 'GET',
			params
		}))
	},

	detailNotice (no) {
		return CommonAxios(({
			url: `api/notice/`,
			method: 'GET',
			params: {
				no
			}
		}))
	}
}
